import React from "react";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import { PageSection } from "../../containers/PageSection";
import { MockVideoDatastore } from "../../MockVideoDatastore";
import TypographyTitle, { TYPOGRAPHY_TITLE_KIND } from "../../components/pixyComponents/typography/TypographyTitle";
import { PixyVideoCard } from "../../components/pixyComponents";
import video1 from "../../assets/images/video-1.png";
import video1Image from "../../assets/images/video-1mark.png";
import discord from "../../assets/images/discord.png";
import yotubu from "../../assets/images/yotubu.png";
import twitter from "../../assets/images/twitter.png";

const FeaturedProjects = () => {
  return (
    <PageSection id="featured-projects">
      <Box sx={{ my: 4, display: "flex", justifyContent: "center", marginBottom: "80px" }}>
        <TypographyTitle kind={TYPOGRAPHY_TITLE_KIND.Secondary}>
          Featured Projects
        </TypographyTitle>
      </Box>
      <Grid container spacing="12">
        {/* {Object.keys(MockVideoDatastore).map((key: string) => {
          const { image, ...rest } = MockVideoDatastore[key];
          return <Grid item xs={12} md={12}><PixyVideoCard key={image} image={image} {...rest} /></Grid>;
        })} */}
        <Grid item xs={12} md={12}>
          <Box sx={{
            padding: "8% 15%",
            backgroundImage: `url(${video1})`,
            backgroundSize: "cover"
          }}>
            <TypographyTitle kind={TYPOGRAPHY_TITLE_KIND.Secondary}>
              MVP
            </TypographyTitle><br></br>
            <Box sx={{
              fontSize: "20px",
              color: "white"
            }}>
              As part of our roadmap we will<br></br>release an MVP with a live project.
                <br></br><br></br>
                <b>Danku Zone</b> is a popular tutorial series<br></br>
                where crypto investors can get alpha on<br></br>
                new protocols, dApps as well as learn the<br></br>
                latest strategies on crypto investing across<br></br>
                main chains.<br></br><br></br>
            </Box>
            <Box sx={{display: "flex"}}>
              <img src={video1Image} className="rounded-full" width={66} />
              <Box sx={{fontSize: "16px", color: "white", marginLeft:"12px" }}>
                danku_r<br></br>
                Video creator and host<br></br>
                <Box sx={{display: "flex"}}>
                  <a href="" style={{marginRight: "6px"}}>
                    <img src={twitter} width={20} />
                  </a>
                  <a href="" style={{marginRight: "6px"}}>
                    <img src={yotubu} width={20} />
                  </a>
                  <a href="">
                    <img src={discord} width={16} />
                  </a>
                </Box>
              </Box>
            </Box>
          </Box>
        </Grid>
      </Grid>
    </PageSection>
  );
};

export default FeaturedProjects;
